<template>
  <div class="ui-wrap m-wrap">
    <AppHeader title="消息中心" showBack="true"></AppHeader>
    <div class="message-wrap">
        <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
          <mt-cell-swipe
                  v-for="(item, index) in messageList"
                  :key="index"
                  :title="item.message_title"
                  :label="item.content"
                  :right="messageRight(item)">
          </mt-cell-swipe>
        </mt-loadmore>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import {getMessageList, removeOneMessage} from '../../../api/home'
import {Toast} from 'mint-ui'
export default {
  name: 'Message-Page',
  data () {
    return {
      messageList: [],
      page: 0,
      allLoaded: false
    }
  },
  created () {
    this.getDatas()
  },
  methods: {
    getDatas () {
      this.$indicator.open({
        text: '加载中'
      })
      this.$set(this, 'page', (this.page + 1))
      getMessageList(this.page)
        .then(data => {
          if (data.state) {
            this.$indicator.close()
//            this.$set(this, 'messageList', data.data.list)
            this.messageList.push(...data.data.list)
            if ((this.page * 12) > data.page) {
              this.allLoaded = true
            }
          }
        })
        .catch(err => console.log(err))
    },
    messageRight (item) {
      if (!item.read_time) {
        return [
          {
            content: '阅读',
            style: {background: '#81d573', color: '#fff'},
            handler: () => this.$messagebox('阅读')
          },
          {
            content: '查看',
            style: {background: '#6495de', color: '#fff'},
            handler: () => this.$router.replace(item.url)
          },
          {
            content: '删除',
            style: {background: 'red', color: '#fff'},
            handler: () => this.deleteMessage(item.message_id)
          }
        ]
      } else if (item.url) {
        return [
          {
            content: '查看',
            style: {background: 'red', color: '#fff'},
            handler: () => this.$router.replace(item.url)
          },
          {
            content: '删除',
            style: {background: 'red', color: '#fff'},
            handler: () => this.deleteMessage(item.message_id)
          }
        ]
      }
    },
    deleteMessage (messageID) {
      removeOneMessage(messageID)
        .then(data => {
          if (data.state) {
            Toast(data.errormsg)
            this.getDatas()
          } else {
            Toast(data.errormsg)
          }
        })
    },
    loadBottom () {
      this.getDatas()
      this.$refs.loadmore.onBottomLoaded()
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
.message-wrap{
  background-color: #e5e5e5;
  a {
    margin-top: .6rem;
  }
}
.ui-wrap:after{
    display: block;
    width: 100%;
    margin-top: 1rem;
    font-size: .8rem;
    content: '';
    border-bottom: 1px solid #ddd;
    color: #999;
    text-align: center;
}
</style>



